@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
*{
  font-family: "Poppins", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  background-color: aliceblue;
}
.password-generator{
  max-width: 400px;
  background-color: #fff;
  margin: 50px auto;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}
.password-generator h2{
  margin-bottom: 20px;
  text-align: center;
  color: #007bff;
  font-weight: 500;
  font-size: 20px;
  text-transform: uppercase;
  border-bottom: 2.5px dashed;
  border-color:#8a8383 ;
}
.input-group,
.checkbox-group,
.generated-password{
  margin-bottom: 15px;
}
.checkbox-group{
  display: flex;
  align-items: center;
}
.checkbox-group input{
  margin-right: 5px;
}
.input-group label{
display: block;
font-weight: bold;
margin-bottom: 10px;
}
input[type="number"]{
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  outline: none;
}
.generate-btn{
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.5s;
}
.generate-btn:hover{
  background-color: #0056b3;
}
.generated-password{
  margin-top: 10px;
  display: flex;
  border: 1px solid #ccc;
  border-radius: 4px;
  overflow: hidden;
}
input[type="text"]{
  border: none;
  flex:1;
  padding: 10px;
  outline: none;
}
.copy-btn{
  border:none;
  padding: 5px;
  background-color: green;
  color: #fff;
}
.footer{
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer a{
  text-decoration: none;
  color:#007bff;
  font-weight: 500;
}
